//Api
const baseUrl = 'http://47.101.163.135:22742' //Api'http://47.101.163.135:22742'
const authorizationScheme = 'Bearer '

export default class Request {
	http(param) {
		// 
		var url = param.url,
			method = param.method,
			header = {},
			data = param.data || {},
			hideLoading = false;

		//
		var requestUrl = baseUrl + url;

		//:GETPOST(POST
		if (method) {
			method = method.toUpperCase(); //

			//GETUrl
			//OrganizationUnitIdsOrganizationUnitIds=[code,code...]OrganizationUnitIds=code&OrganizationUnitIds=code...
			if (method == 'GET' && data) {
				const qs = require('qs')
				requestUrl += '?' + qs.stringify(data)
				data = {}
			}

			header = {
				'content-type': 'application/json', //'application/x-www-form-urlencoded' //
				//
				'Authorization': uni.getStorageSync('loginInfo') ? authorizationScheme + uni.getStorageSync(
					'loginInfo').accessToken : ''
			};
		}

		//
		if (!hideLoading) {
			hideLoading = true
			uni.showLoading({
				title: '...'
			})
		}

		// promise
		return new Promise((resolve, reject) => {
			//token
			if (uni.getStorageSync('expireTime') && uni.getStorageSync('expireTime') < Date.now()) {
				uni.redirectTo({
					url: '/pages/login/login?isShowToast=1',

				})
				resolve();
				return;
			}

			var errorMsg = ''
			var isRedirectToLogin = false
			// 
			uni.request({
				url: requestUrl,
				data: data,
				method: method,
				header: header,
				success: (res) => {
					//  api 
					if (res.statusCode == 200) {
						// 
						resolve(res.data);
						return;
					} else {
						if (res.data.unAuthorizedRequest) {
							errorMsg = res.data.error.message

							isRedirectToLogin = true
						} else {
							if (res.statusCode == 400 || res.statusCode == 401 || res
								.statusCode ==
								403) {
								errorMsg = res.data.error.message
							} else if (res.statusCode == 404) {
								errorMsg = ''
							} else if (res.statusCode == 500) {
								errorMsg = res.data.error.details
							}
						}

						return;
					}
				},
				//
				fail: (e) => {
					errorMsg = ''
					resolve(e.data);
				},
				//
				complete() {
					//
					if (hideLoading) {
						hideLoading = false
						uni.hideLoading({
							fail: function(res) {
								//failhideLoading
								console.log('')
							}
						});
					}

					//
					if (errorMsg) {
						uni.showToast({
							icon: 'none',
							title: errorMsg
						})
					}

					//
					if (isRedirectToLogin) {
						setTimeout(() => {
							uni.redirectTo({
								url: '/pages/login/login',
							})
						}, 1500)
					}

					resolve();
					return;
				}
			})
		})
	}
}
